.demo {
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 40px;
  padding: 10px;
  border: 1px solid #eee;
  margin-bottom: 20px;
}

.controls {
  display: grid;
  grid-gap: 20px;
}

.label {
  margin-right: 15px;
}



